<template>
  <div class="header">
    <div class="head">
      <div class="flex_c">
        <router-link to="/">
          <img class="logo" src="../assets/img/logo.png">
        </router-link>
        <ul class="nav">
          <li v-for="item in nav" :key="item.id" class="item" :class="{active: isActive===item.id}" @click="select_nav(item.id)">
            <router-link :to="item.link">{{ item.name }}</router-link>
          </li>
        </ul>
      </div>
      <div class="nav_r flex_c">
        <div class="search_out" :class="{search_focus: isShow===true}">
          <div class="search_inn flex_c">
            <i class="icon iconfont icon-header_icon_search_" @click="goSearch()"></i>
            <input maxlength="128" type="text" placeholder="搜索音乐/MV/歌单/歌手" v-model="searchValue" class="search" @keyup.enter="goSearch()" @blur="isShow=false" @focus="isShow=true">
          </div>
          <div class="list" :class="{hot_key:isShow===true}" v-show="isShow===true">
            <p class="tit" v-show="searchValue===''">大家都在搜</p>
            <ul ref="searchList">
              <template v-if="isShow===true">
                <li v-for="(name,index) in searchList" class="list_item reg_text" :key="index">
                  <span class="idx" v-show="searchValue===''">{{ index + 1 }}</span>
                  <span class="key_text">{{ name }}</span>
                </li>
              </template>
            </ul>
          </div>
        </div>
        <div class="reg_text">
          <span>登录</span>
          <span>/</span>
          <span>注册</span>
        </div>
      </div>
    </div>
  </div>

</template>

<script>
export default {
  name: "MusicNav",
  data () {
    return {
      searchValue: "",
      nav: [{
        id: 0,
        name: "首页",
        link: "/"
      },
      {
        id: 1,
        name: "排行榜",
        link: "/rankList"
      },
      {
        id: 2,
        name: "歌单",
        link: "/playlists"
      },
      {
        id: 3,
        name: "歌手",
        link: "/singers"
      },
      {
        id: 4,
        name: "我的音乐",
        link: "/play"
      }],
      searchList: [
        "金玉良缘",
        "test1",
        "test2"
      ],
      isActive: 0,
      isShow: false,
    }
  },
  methods: {
    select_nav (i) {
      this.isActive = i;
    },
    //跳转搜索页面
    goSearch () {
      this.$store.commit("setSearchword", this.searchValue)
      this.$router.push({ path: '/search/song', query: { key: this.searchValue } })
    }
  },
  watch: {
    "$route.query.key": {
      handler (newVal, oldVal) {
        this.searchValue = newVal;
      },
      immediate: true, //监听首次绑定值
    }
  },
  // mounted () {
  //   this.appendChild();
  // }
}

</script>

<style lang="scss" scoped>
@import "../assets/css/nav.scss";
</style>



