<template>
  <div id="play">
    <div class="play_bar" ref="play">
      <!-- 播放、上一首、下一首 -->
      <div class="col_l flex_c" :class="{no_list:playList.length===0}">
        <div style="width:100%;height:100%; position: absolute;z-index:10" v-if="playList.length===0"></div>
        <i title="上一首" class="iconfont icon-bar_icon_pre_" @click="getPreSong()"></i>
        <i id='play' :title="isPlay?'暂停':'播放'" class="iconfont" :class="isPlay?'icon-bar_icon_pause_':'icon-bar_icon_play_1'" @click="setPlayOrPause()"></i>
        <i title="下一首" class="iconfont icon-bar_icon_next_" @click="getNextSong()"></i>
      </div>
      <div class="col_c flex_c">
        <div style="width:100%;height:100%; position: absolute;z-index:10" v-if="playList.length===0"></div>
        <div class="cover_out">
          <img class="pic" :src="songInfo.pic||defaultImg" @click="goPlayPage(songInfo.rid)" />
        </div>
        <!-- 歌曲信息 -->
        <div>
          <div class="songInfo flex_c">
            <div class="info flex_c">
              <span class="song_name" @click="goPage('music',songInfo.rid)">{{songInfo.name||'Music'}}</span>
              <span class="artist"> - {{songInfo.artist}}</span>
            </div>
            <span class="time">{{playTime}}/{{songInfo.songTimeMinutes||'00:00'}}</span>
          </div>
          <!-- 播放条 -->
          <div class="process">
            <div class="progress_bar" @mousedown="progressMove($event,'progress')">
              <div class="progress" ref='progress'>
                <div class="progress_btn" @mousedown="progressBtnMove('progress')"></div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <!-- 控制面板 -->
      <div class="col_r flex_c">
        <i title="收藏歌曲" class="prev icon_heart iconfont icon-bar_icon_heart_"></i>
        <i title="下载歌曲" class="prev iconfont icon-bar_icon_download_" @click="download()"></i>
        <span @click="playMode++;if(playMode>3) playMode=0;">
          <i title="循环播放" class="prev iconfont icon-bar_icon_loop_" v-show="playMode===0"></i>
          <i title="单曲循环" class="prev iconfont icon-bar_icon_repeat_" v-show="playMode===1"></i>
          <i title="顺序播放" class="prev iconfont icon-bar_icon_list_" v-show="playMode===2"></i>
          <i title="随机播放" class="prev iconfont icon-bar_icon_random_" v-show="playMode===3"></i>
        </span>
        <span class="playList" @click="isShow=!isShow" ref="playListBtn">
          <span class="play_count" v-if="playList.length!=0">{{playList.length}}</span>
          <i title="歌曲列表" class="prev iconfont icon-bar_icon_playlistfuzhi"></i>
        </span>
        <span @click="setVolume()">
          <i title="静音" class="iconfont icon-bar_icon_mute" v-show="!isVolume"></i>
          <i title="音量" class="iconfont icon-bar_icon_volume_" v-show="isVolume"></i>
        </span>
        <div class="volume">
          <div class="progress_bar" @mousedown="progressMove ($event, 'volume')">
            <div class="progress" ref='volume' :style="'width:'+realVolume+'px'">
              <div class="progress_btn" @mousedown="progressBtnMove('volume')" :title="realVolume"></div>
            </div>
          </div>
        </div>
      </div>

      <!-- 播放列表 -->
      <div class="play_list" v-show="isShow" ref="playList">
        <div class="top flex_c">
          <span>播放列表</span>
          <div class="flex_c">
            <div class="flex_c">
              <i class="iconfont icon-list_clean_n"></i>
              <span>清空列表</span>
            </div>
            <i class="close iconfont icon-list_icon_x" @click="isShow=false"></i>
          </div>
        </div>
        <music-table :songList="playList" :playFunc="getSongInfo" class="list">
          <template v-slot:opts>
            <i class="iconfont icon-list_clean_n"></i>
          </template>
        </music-table>

      </div>
    </div>
    <audio ref="audio" :src="songUrl" @timeupdate="timeUpdate" @ended="audioEnd"></audio>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
import { HttpManager } from '../api';
import mixin from '../mixins';
import Storage from '../storage'
import MusicTable from './MusicTable.vue';
export default {
  components: { MusicTable },
  name: "MusicPlay",
  mixins: [mixin],
  data () {
    return {
      // isPlay: false, // 是否播放
      playTime: '00:00',
      isShow: false,
      playMode: 0,
      isVolume: true,
      defaultImg: require('../assets/img/default_img.png'),
      tempVolume: 0,
      realVolume: 50,
      playList: [],
      index: 0,
    }
  },
  created () {

  },
  methods: {
    setPlayOrPause () {
      this.$store.commit('setIsPlay', !this.isPlay);
      // this.isPlay = !this.isPlay;
      // this.isPlay ? audio.play() : audio.pause();
    },
    // 音频停止后, 把喇叭置灰
    audioEnd () {
      // this.isPlay = false;
      // this.$refs.audio.currentTime = 0;
      // this.$store.commit('setCurTime', 0);
      this.index = Storage.getLocalStorage('play_song_index');
      switch (this.playMode) {
        // 循环播放
        case 0:
          this.index += 1;
          if (this.index >= this.playList.length) {
            this.index = 0;
          }
          this.getSongInfo(this.index);
          break;
        // 单曲循环
        case 1: this.getSongInfo(this.index); break;
        // 顺序播放
        case 2:
          this.index += 1;
          if (this.index >= this.playList.length) {
            this.index = this.playList.length - 1;
            this.$store.commit('setIsPlay', false);
          }
          break;
        // 随机播放
        case 3:
          //Math.floor()向下取整，随机0-(this.playList.length-1)
          this.index = Math.floor(Math.random() * this.playList.length);
          this.getSongInfo(this.index);
          break;
        default: break;
      }
      Storage.setLocalStorage('play_song_index', this.index);
      // this.$store.commit('setIsPlay', false);
    },
    download () {
      // 当前页面打开资源链接，浏览器会自动下载
      window.location.href = this.songUrl;
    },
    setVolume () {
      let audio = this.$refs.audio;
      this.isVolume = !this.isVolume;
      if (!this.isVolume) {
        this.tempVolume = Storage.getLocalStorage('volume');
        this.realVolume = 0;
        Storage.setLocalStorage('volume', 0);
      }
      else {
        this.realVolume = this.tempVolume;
        Storage.setLocalStorage('volume', this.tempVolume);
      }
      audio.volume = this.realVolume * (1 / 100);
    },
    floatToTime (val) {
      let min = (val / 60) | 0;
      let sec = (val % 60) | 0;
      let time;
      if (min < 10) {
        time = '0' + min + ':';
      } else {
        time = min + ':';
      }
      if (sec < 10) {
        time += '0' + sec;
      } else {
        time += sec;
      }
      return time;
    },
    timeUpdate () {
      let audio = this.$refs.audio;
      this.playTime = this.floatToTime(audio.currentTime);
      audio.volume = this.realVolume * (1 / 100);
      //设置进度条位置
      this.$refs.progress.style = `width:${audio.currentTime / (audio.duration / 340)}px`;
      this.$store.commit('setCurTime', audio.currentTime)
      //重置歌词位置，播放时间不能直接判断等于0，触发事件时，播放时间一般大于0
      if (audio.currentTime <= 1)
        this.$store.commit('setIsLrc', 0);
    },

    //点击进度条，设置进度
    progressMove (event, type) {
      let scrollx = document.documentElement.scrollLeft || document.body.scrollLeft;
      let audio = this.$refs.audio;

      if (type === 'volume') {
        let x = event.clientX + scrollx - this.$refs.volume.getBoundingClientRect().left
        //设置播放时间
        audio.volume = x * (1 / 100);
        this.realVolume = x | 0;
        Storage.setLocalStorage('volume', this.realVolume);
        this.$refs.volume.style = `width:${x}px`;
      }
      if (type === 'progress') {
        let x = event.clientX + scrollx - this.$refs.progress.getBoundingClientRect().left
        //设置播放时间
        audio.currentTime = x * (audio.duration / 340);
      }
      // this.playTime = this.floatToTime(audio.currentTime);
      // this.$refs.progress.style = `width:${x}px`;
    },
    //拖动进度按钮设置进度
    progressBtnMove (type) {
      let that = this;
      let scrollx = document.documentElement.scrollLeft || document.body.scrollLeft;
      //getBoundingClientRect()返回类型DOMRect对象,包括元素大小、左上角坐标和相对浏览器视窗的距离
      // right是指元素右边界距窗口最左边的距离，bottom是指元素下边界距窗口最上面的距离
      // let left = that.$refs.progress.getBoundingClientRect().left
      let audio = that.$refs.audio;
      document.onmousemove = (event) => {
        if (type === 'volume') {
          let x = event.clientX + scrollx - this.$refs.volume.getBoundingClientRect().left
          if (x > 100) {
            x = 100;
          } else if (x < 0) {
            x = 0;
          }
          //设置播放音量
          audio.volume = x * (1 / 100);
          this.realVolume = x | 0;
          Storage.setLocalStorage('volume', this.realVolume);
          this.$refs.volume.style = `width:${x}px`;
        }
        if (type === 'progress') {
          let x = event.clientX + scrollx - this.$refs.progress.getBoundingClientRect().left
          if (x > 340) {
            x = 340;
          } else if (x < 0) {
            x = 0;
          }
          //设置播放时间
          audio.currentTime = x * (audio.duration / 340);
        }
        // that.playTime = that.floatToTime(audio.currentTime);
        // that.$refs.progress.style = `width:${x}px`;
      }
      document.onmouseup = (event) => {
        document.onmousemove = null;
        document.onmouseup = null;
      }
    },
    // 获取列表中歌曲信息且不改变歌曲顺序
    getSongInfo (index) {
      let that = this;
      //暂停播放
      this.$store.commit('setIsPlay', false);
      this.$store.commit('setSongInfo', Storage.getLocalStorage('song_list')[index]);
      this.$store.commit('setPlaySong', this.playList[index].rid);
      HttpManager.getPlayUrl({ mid: Storage.getLocalStorage('song_list')[index].rid, type: 'music' })
        .then((res) => {
          // that.status = parseInt(res.status);
          that.$store.commit('setSongUrl', res.data.url);
          that.$store.commit('setIsPlay', true);
        })
        .catch(() => { });
    },
    getPreSong () {
      this.index = Storage.getLocalStorage('play_song_index') || 0;
      // 只有随机播放对上下首歌曲切换有影响
      if (this.playMode === 3) {
        //Math.floor()向下取整，随机0-(this.playList.length-1)
        this.index = Math.floor(Math.random() * this.playList.length);
      } else {
        this.index -= 1;
        if (this.index < 0)
          this.index = this.playList.length - 1;
      }
      Storage.setLocalStorage('play_song_index', this.index);
      this.getSongInfo(this.index);
    },
    getNextSong () {
      this.index = Storage.getLocalStorage('play_song_index') || 0;
      if (this.playMode === 3) {
        //Math.floor()向下取整，随机0-(this.playList.length-1)
        this.index = Math.floor(Math.random() * this.playList.length);
      } else {
        this.index += 1;
        if (this.index >= this.playList.length)
          this.index = 0;
      }
      Storage.setLocalStorage('play_song_index', this.index);
      this.getSongInfo(this.index);
    },
  },
  computed: {
    ...mapGetters([
      'songInfo',
      'songUrl',
      'isPlay',
    ])
  },
  watch: {
    isPlay: {
      handler (newVal, oldVal) {
        let audio = this.$refs.audio;
        // console.log(newVal);
        if (newVal === true) {
          //加载歌曲
          // audio.load();
          //等歌曲加载后播放
          setTimeout(() => {
            audio.play();
          }, 200);
        } else
          audio.pause();
        // 刷新播放列表
        this.playList = Storage.getLocalStorage('song_list');
      }
    },
  },
  mounted () {
    let that = this;
    // 固定在底部的播放栏随滚动条滚动
    window.onscroll = () => {
      let sl = -(document.documentElement.scrollLeft || document.body.scrollLeft);
      that.$refs.play.style.left = sl + 'px';
    }
    window.onclick = (event) => {
      // 如果在播放列表和按钮外点击，则隐藏播放列表
      if (!this.$refs.playList.contains(event.target) && !this.$refs.playListBtn.contains(event.target)) {
        this.isShow = false;
      }
    }
    this.$store.commit('setIsPlay', false);
    if (Storage.getLocalStorage('volume') === null)
      Storage.setLocalStorage('volume', 50);//设置默认音量
    this.$refs.audio.volume = Storage.getLocalStorage('volume') * (1 / 100);
    this.realVolume = Storage.getLocalStorage('volume');
    if (Storage.getLocalStorage('play_song_index') != null) {
      this.index = Storage.getLocalStorage('play_song_index');
    } else {
      Storage.setLocalStorage('play_song_index', 0);
    }
    if (Storage.getLocalStorage('song_list') != null) {
      // 获取播放列表
      this.playList = Storage.getLocalStorage('song_list');
      this.$store.commit('setSongInfo', this.playList[this.index]);
      this.$store.commit('setPlaySong', this.playList[this.index].rid);
      HttpManager.getPlayUrl({ mid: this.playList[this.index].rid, type: 'music' })
        .then((res) => {
          // that.status = parseInt(res.status);
          that.$store.commit('setSongUrl', res.data.url);
        })
        .catch(() => { });
    }

  },
  beforeDestroy () {//注意：监听的函数要在关闭页面时销毁掉,不然会影响到其它页面
    window.onscroll = null;
    window.onclick = null;
  },
}

</script>

<style lang="scss" scoped>
@import "../assets/css/font";
span {
  display: block;
  color: #333;
}
.iconfont {
  color: #333;
  font-size: 24px;
}
.play_bar {
  -webkit-user-select: none;
  -webkit-text-size-adjust: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;

  position: fixed;
  bottom: 0;
  z-index: 50;
  width: 100%;
  min-width: 990px;
  max-width: 1490px;
  height: 70px;
  background: rgba(250, 250, 254, 0.96);
  padding: 0 120px;
  display: flex;
  justify-content: space-between;
  .play_list {
    position: absolute;
    right: 14%;
    bottom: 80px;
    width: 512px;
    background: #fff;
    padding: 34px 0 20px;
    box-shadow: 0 4px 20px 0 rgb(65 67 70 / 8%);

    .list {
      height: 256px;
      padding: 5px;
    }
  }
  .col_l {
    position: relative;
    width: 200px;
    i {
      margin: 0 20px;
    }
    #play {
      &.iconfont {
        font-size: 40px;
      }
    }
    &.no_list .iconfont {
      color: #999;
      :hover {
        color: #999;
      }
    }
  }
  .col_c {
    position: relative;
    width: 440px;
    .cover_out {
      height: 70px;
      width: 100px;
      // padding: 5px;
      padding: 0 15px;
      .pic {
        width: 100%;
        height: 100%;
      }
    }
    .songInfo {
      width: 340px;
      font-size: 14px;
      margin-bottom: 10px;
      .info {
        width: 260px;
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
        .artist {
          color: #666;
        }
      }
      .time {
        color: #999;
      }
    }
    .process {
      width: 340px;
    }
  }
  .col_r {
    position: relative;
    width: 360px;
    margin-left: 20px;
    i {
      margin: 0 10px;
    }
    .iconfont {
      font-size: 18px;
    }
    .playList {
      position: relative;
      .play_count {
        position: absolute;
        text-align: center;
        top: -4px;
        left: 18px;
        padding: 0 2px;
        font-size: 12px;
        // background: #fff;
        cursor: pointer;
      }
    }
    .volume {
      margin-left: 10px;
      width: 100px;
    }
  }
}
.progress_bar {
  position: relative;
  width: 100%;
  height: 3px;
  background: rgba(0, 0, 0, 0.05);
  border-radius: 2px;
  cursor: pointer;
  .progress {
    position: absolute;
    top: 0;
    left: 0;
    background: #31c27c;
    height: 3px;
    // width: 10px;
    border-radius: 2px;
  }
  .progress_btn {
    position: absolute;
    z-index: 10;
    right: -8px;
    width: 12px;
    height: 12px;
    top: -4px;
    background: #fff;
    box-shadow: 0 0 15px 0 rgb(0 0 0 / 15%);
    border-radius: 50%;
    &:after {
      position: absolute;
      content: "";
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 4px;
      height: 4px;
      background: #31c27c;
      border-radius: 50%;
    }
  }
}
.flex_c {
  display: flex;
  align-items: center;
}
</style>
<style lang ='scss'>
.play_list {
  .list {
    .song_list {
      height: 200px;
      overflow: auto;
      // 隐藏滚动条
      scrollbar-width: none; /* firefox */
      -ms-overflow-style: none; /* IE 10+ */
      &::-webkit-scrollbar {
        display: none; /* Chrome Safari */
      }
      .song_item {
        height: 45px !important;
        .song_opts {
          width: 120px !important;
        }
      }
    }
  }
}
</style>



